import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { IfxEvalRegisterComponent } from './ifx-eval-register.component';

const routes: Routes = [
  {
    path: '',
    component: IfxEvalRegisterComponent,
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class IfxEvalRegisterRoutingModule {}
