import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { BrandingService, FieldDefinition, MetaDataType } from 'shared-code-lib';
import { InterFAXService } from '../../blocks/services/interfax.service';
import { ApiResult } from '../../blocks/services/models';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';

@Component({
  selector: 'app-ifx-eval-register',
  templateUrl: './ifx-eval-register.component.html',
  styleUrls: ['./ifx-eval-register.component.scss'],
})
export class IfxEvalRegisterComponent implements OnInit {
  fieldEmail: FieldDefinition;
  email = '';
  registered: boolean = false;
  @ViewChild('loginComponent', { static: true })
  loginDiv: ElementRef;
  errors = [];
  logoUrl: string;

  constructor(
    private translateService: TranslateService,
    private interfaxService: InterFAXService,
    private spinnerStateService: SpinnerStateService,
    private brandingService: BrandingService
  ) {}

  ngOnInit(): void {
    this.fieldEmail = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('IfxEval.Email'),
      required: true,
    });

    this.logoUrl = this.brandingService.getBrandDetails()?.loginLogoUrl;
  }

  startRegisteration() {
    this.spinnerStateService.show(
      this.translateService.instant('IfxEval.StartingRegistrationProcess'),
      this.loginDiv.nativeElement
    );
    this.interfaxService.startEvaluationAccountSignup(
      this.email,
      this.startRegisterationSuccess,
      this.startRegisterationFail
    );
  }

  startRegisterationFail = (err: any) => {
    this.spinnerStateService.hide();
  };

  startRegisterationSuccess = (result: ApiResult) => {
    this.errors = [];
    this.spinnerStateService.hide();
    if (result.success) {
      this.registered = true;
    } else {
      this.errors.push(result.errors);
    }
  };
}
